@import '../../utils/variables';


.button {
    width: 100px;
    height: 75vh;
    border: none;
    background: $colorMain;
    transition: 0.5s;
    &:first-child {
        margin-right: 5px;
    }
    &:last-child {
        margin-left: 5px;
    }
    &:hover {
        cursor:pointer;
        background:  #e0e0e0;
        outline: 0;
        outline-offset: 0;
    }
    &:active {
        cursor:pointer;
        background:  #e0e0e0;
        outline: 0;
        outline-offset: 0;
    }
    &:focus {
        cursor:pointer;
        background:  #e0e0e0;
        outline: 0;
        outline-offset: 0;
    }

}
.wrapper {
    display: flex;
    flex-direction: column;
    .cardItem {
        transition: all 1.5s;
        transform: scale(1);
        opacity: 1;
        .red {
            color:#c31d1d;
        }
        .green {
            color:#008000;
        }
        .orange {
            color: #ffa500;
        }
        .yellow {
            color: #949813;
        }
        .blue {
            color: #004cff;
        }
        .violet {
            color: #ee82ee;
        }
        .main {
            color: $colorEnglishWord;
        }
    }
    .animation {
        transform: scale(0.5);
        opacity: 0;
    }
    .counter {
        align-self: center;
        margin-top: 50px;
        margin-bottom: 50px;
        font-size: 25px;
    }
}
